@import '../../styles/variables.scss';

.form-contact {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width: 350px;
    padding-top: 30px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 30px;
    box-shadow: 0 5px 15px rgba(88, 88, 88, 0.6);
    border-radius: 10px;
    z-index: 1;
    
}

.form-contact__group {
    display: flex;
    flex-direction: column;
}

.form-contact__group__input {
    font-size: 16px;
    margin: 0 auto;
    padding: 5px 10px;
    border-radius: 5px;
    background-color: rgb(255, 255, 255);
    border: none;
    color: black;
    width: 90%;
    // border-bottom: 0.3rem solid transparent;
    transition: all 0.3s;
    font-family: 'Lato', sans-serif;
}



.form-contact__group__textarea {
    font-size: 16px;
    width: 90%;
    height: 90px;
    resize: none;
    margin: 0 auto;
    margin-bottom: 32px;
    padding: 5px 10px;
    border-radius: 5px;
    background-color: rgb(255, 255, 255);
    border: none;
    transition: all 0.3s;
    font-family: 'Lato', sans-serif;
}

.form-contact__group__input__error{
    transition: 0.3s;
    background-color: rgba(255, 0, 0, 0.686);
}

.form-contact__group__label {
    padding: 0 10px;
    font-size: 12px;
    width: 90%;
    margin: 0 auto;
    transition: all 0.3s;
    margin-top: 5px;
    transform: translateY(-3.3rem);
    font-family: 'Lato', sans-serif;
    // transform: translateY(-4.3rem);
}

