.form-contact__group__input:placeholder-shown + .form-contact__group__label {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translateY(-3rem);
    transform: translateY(-3rem);
}

.form-contact__button {
    width: calc(90% + 20px);
    background-color: white;
    border: none;
    border-radius: 5px;
    padding: 5px 10px;
    margin: 0 auto;
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    color: rgb(106, 106, 106);
    transition: 1s;

    &:disabled {
        transition: 1s;
        background-color: rgba(255, 255, 255, 0.535);
        opacity: 0.2;
        color: white;
    }
}

@media (min-width: $landscape-phones-width) {
    .form-contact {
        width: 350px;
    }
}

@media (min-width: $tablet-width) {
    .form-contact {
        width: 450px;
    }
}
@media (min-width: $desktop-width) {
    .form-contact {
        width: 550px;
    }
}